.acWrapper{
    width: 400px;
    color:  #EC5F70;
    position: relative
}
.adWrapper h2{
    font-size: 40px;
    margin-bottom: 0;
}
.fillDetails{
    color: rgb(126, 123, 123);
    margin: 0 0 20px 0;
}
.school p{
    font-size: 20px;
    margin: 0 0 10px 0;
}

.school input{
    border: 1px solid #EC5F70;
    border-radius: 4px;
    padding: 20px 15px;
    width: 550px;
}
.classBoards{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 35px;
}
.classBoards p{
    font-size: 20px;
}
.classBoards input{
    border: 1px solid #EC5F70;
    border-radius: 4px;
    padding: 20px 15px;
    width: 240px;
}
.subjects{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.subjectsp{
    font-size: 20px;
}

.subjectList{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
}
.subjectList p{
    border: 1px solid #EC5F70;
    border-radius: 18px;
    padding: 15px 15px;
    width: 150px;
    margin: 0 0 0 0;
}
.next button{
        width: 250px;
        height: 50px;
        margin: 70px 0 0 330px;
        border: 1px solid #EC5F70;
        border-radius: 4px;
        background-color: #EC5F70;
        color: white;
        font-size: 17px;
        cursor: pointer;
        
}