.pd{
    font-size: 30px;
    color: rgba(245, 51, 74, 0.70);
    font-weight: 700;
    margin: 17px 0 0 0;
    letter-spacing: 1px;
}

.fd{
    margin: 0 0 6px 0;
    color: rgb(181, 177, 177);
    font-size: 19px;
    letter-spacing: 1px;
}

.text1 , .text2  p{
    margin: 28px 0 6px 0;
    color: rgba(245, 51, 74, 0.70);
    font-weight: 700;
    position: relative;
}

.text1 input{
    display: flex;
    width: 200px;
    height: 30px;
    padding: 15px 23px;
    align-items: center;
    gap: 10px;
    border-radius: 4px;
    border: 1px solid #EC5F70;
    background: #FFF;
}
.dob{
    display: flex;
    width: 200px;
    height: 30px;
    padding: 15px 23px;
    align-items: center;
    gap: 7px;
    border-radius: 4px;
    border: 1px solid #EC5F70;
    background: #FFF;

}
.dob input{
    border: 0;
    letter-spacing: 3px;
}

.group34216{
    color: rgba(245, 51, 74, 0.70);
    font-weight: 700;
    display: flex;
    flex-direction: row;

}

.gender{
    margin: 28px 0 6px 70px;
}

.group34212{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-left: 68px;
    gap: 8px;
}

.group34213{
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border: 1px solid #EC5F70;
    border-radius: 12px;
    padding: 0 10px 0 10px;
}

.mobile{
    padding: 8px 0 0 320px;
    color: #EC5F70;
    font-family: Poppins;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.number{
    display: flex;
    width: 270px;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border: 1px solid #EC5F70;
    border-radius: 4px;
    gap: 10px;
    padding-left: 10px;
}

.number input{
    border: none;
    font-size: 17px;
    letter-spacing: 4px;
}

.next button{
    width: 250px;
    height: 50px;
    margin: 76px 0 0 27px;
    border: 1px solid #EC5F70;
    border-radius: 4px;
    background-color: #EC5F70;
    color: white;
    font-size: 17px;
    cursor: pointer;
    
}